
<style scoped>
.h-center {
  margin-left: auto;
  margin-right: auto;
}

.starting {
  text-align: center;
  color: gray;
}

.progress {
  font-size: 40px;
}
</style>


<template>
  <div class="h-center starting">
    <div class="h-center logo">
      <img src="/logo.png" />
      <h3>请稍候</h3>
    </div>
    <div class="h-center progress">
      <i class="el-icon-loading"></i>
    </div>
    <div>
      <pre> {{currentSession}} </pre>
    </div>
  </div>
</template>

<script>
import { setTimeout } from "timers";
export default {
  name: "starting",

  data() {
    return {
      timer_done: false,
      net_done: false,
      session: null
    };
  },

  created() {
    this.net_done = false;
    this.timer_done = false;
    this.session = null;
    this.refresh();
  },

  computed: {
    currentSession() {
      var session = this.$store.getters["CurrentSession/session"];
      return JSON.stringify(session, null, 4);
    }
  },

  methods: {
    refresh() {
      var self = this;

      var on_ok = resp => {
        self.onSessionStateReady(resp);
      };

      var on_error = res => {
        self.showError(res);
      };

      var type = "CurrentSession";
      var id = null;

      this.$store.dispatch("rest/get", { id, type, on_error, on_ok });
      this.$store.dispatch("CurrentSession/refresh", {});
    },

    onSessionStateReady(res) {
      var state = res.currentSession;
      if (state.authenticated) {
        this.jumpToHome();
      } else {
        this.jumpToLogin();
      }
    },

    showError(res) {
      var js = JSON.stringify(res, null, 4);
      this.$message("error: " + js);
    },

    jumpToLogin() {
      this.$router.push("login");
    },

    jumpToHome() {
      this.$router.push("home");
    }
  }
};
</script>
